.recentAll{
    width: 100% !important;
}
.recentComment .postListTitle{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.postListTitle a{
    font-size: 0.8rem;
    color: blue;
}
.recentComment .postTitle{
    font-weight: unset;
    font-size: 0.9rem;
    text-align: right;
    color: gray;
    padding: 0;
    height: 1.2rem;
    overflow: hidden;
}
.recentComment .postBody{
    height: 1.2rem;
}
.recentComment .username{
    font-weight: bold;
    font-size: 1rem;
    padding-left: 8px;
}