#background {
	display: flex;
	height: 100%;
	justify-content: center;
	align-items: center;
	font-size: 1rem;
}
#loginWrapper {
	padding: 40px 0 40px;
	border-radius: 30px;
	border: 1px solid black;
	max-width: 500px;
	width: 100%;
	margin: auto;
	background: white;
}
.login {
	display: flex;
	flex-direction: column;
	justify-content: center;
	max-width: 360px;
	width: 90%;
	margin: auto;
	.text {
		margin-bottom: 10px;
		font-weight: bold;
	}
	input {
		border: 0;
		border-bottom: 1px solid black;
		outline: none;
		font-size: inherit;
		background: transparent;
	}
	.loginLink {
		color: blue;
		text-decoration: underline;
		text-align: right;
		font-size: 0.9rem;
		padding-top: 4px;
	}
	button {
		border: 1px solid black;
		border-radius: 20px;
		background: #42ff42;
		cursor: pointer;
		height: 40px;
		text-align: center !important;
		font-size: inherit;
		outline: none;
	}
}
.googleWarning{
	font-size: 0.8rem;
	margin-top: 4px;
	word-break: break-all;
}
.googleWarning svg{
	font-size: 1rem;
	vertical-align: text-bottom;
}
.googleWarning span{
	color:blue;
}
.googleWarning img{
	width: 16px;
	vertical-align: text-bottom;
}
.center {
	color: black;
	width: 100%;
	font-size: 1rem;
	margin-bottom: 10px;
	font-weight: bold;
	text-align: center;
}
.check {
	height: 21px;
	color: red;
}
