.red {
	background: red !important;
}
.oAuth {
	border-bottom: 2px solid gray;
	padding-bottom: 40px;
	margin-bottom: 40px;
}
.googleLogin{
    color: black !important;
    width: 100%;
	border: 1px solid black !important;
	margin-bottom: 4px;
	box-shadow: unset !important;
}
.googleLogin:hover{
    opacity: 0.7 !important;
    background: rgb(190, 229, 241) !important;
}
.googleLogin div {
    background-color: transparent !important;
	padding: 0 11px !important;
}
.googleLogin span {
	padding: 0 !important;
    font-size: 1.1rem !important;
    font-weight: bold !important;
}
.naverLogin{
	width: 100%;
	height: 40px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	border: 0;
	background: #1ec800;
	border: 1px solid #1aaa00;
	color: white;
	font-weight: bold;
	font-size: 1.1rem;
	margin-bottom: 4px;
	cursor: pointer;
	border-radius: 2px;
	font-family: Roboto, sans-serif;
}
.naverLogin:hover{
	opacity: 0.7 !important;
}
.naverLogin img{
	width: 40px;
	margin-right: 10px;
}
.kakaoLogin{
	background: #fee500;
	color: black;
	border: 1px solid #c0ad00;
}
.kakaoLogin img{
	height: 38px;
}
.googleLogin{
	background: white;
	color: black;
	border: 1px solid black;
}
.googleLogin img{
	height: 38px;
